import "core-js/modules/es.array.push.js";
import { onMounted, reactive, toRefs } from 'vue';
import Header from '../store/components/Header.vue';
import { useStore } from 'vuex';
import { showDialog } from 'vant';
import { useRouter, useRoute } from 'vue-router';
export default {
  components: {
    Header
  },
  setup(props) {
    const store = useStore();
    const router = useRouter();
    const route = useRoute();
    const data = reactive({
      tel: 13267878342,
      name: '李春满',
      totalPrice: 0 //订单商品总金额
    });

    // 计算商品金额
    const initPrice = () => {
      let price = 0;
      store.state.orderList.forEach(item => {
        price += item.num * item.price;
      });
      data.totalPrice = price;
    };

    // 用户信息管理
    const userAddress = () => {
      store.state.userAddress.forEach(item => {
        if (item.isDefault) {
          data.tel = item.tel;
          data.name = item.name;
        }
      });
    };

    // 点击生成订单逻辑
    const handleCreateOrder = () => {
      showDialog({
        title: '提示',
        message: '您的订单已完成！'
      }).then(() => {
        router.push('/order');
        // 要和购物车联系起来
        let newList = store.state.cartList.filter(item => {
          // 筛选出没被选中的商品
          return !route.query.list.includes(item.id + "");
        });
        // 再把没被选中生产订单的商品 重新赋值给 cartList
        store.commit('delete', newList);
        // 把已完成的订单，记录下来
        store.commit('orderListEnd');
      });
    };
    onMounted(() => {
      initPrice(), userAddress();
    });
    const onEdit = () => {
      router.push('/address');
    };
    return {
      ...toRefs(data),
      onEdit,
      store,
      handleCreateOrder
    };
  }
};